*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}

:root {
    font-family: 'Roboto', sans-serif;
}
button{
    outline:none;
    
}
.container{
    display:flex;
    width:100%;
    height:auto;
    color:white;
    padding:1rem 5%;
    align-items: center;
    justify-content: flex-end;
    background-color: black;
}


.logo{
    margin-right:auto;
    cursor:pointer;
}


.navStyle{
    text-decoration: none;
    list-style: none;
    display:inline-block;
}
.nav-links li{
    list-style: none;
    text-align: center;
    width:100%;
    padding:0px 10px 0;
    color:white;
}
.nav-links li:hover{
    color:gray;
}
.contact-btn{
    border:none;
    margin-left:1.5%;
    background-color:white;
    font-weight: 500;
    padding:5px 6px;
    list-style:none;
    text-decoration: none;
    color:black;
    cursor:pointer;
    border:1px solid white;
    
}
.contact-btn:hover {
    color:white;
    background-color:black;
    
}

.container-Ti{
    width:100%;
    height:100%;
    text-align: center;
}
.testItem-Header{
    border-radius: 20px;
    width:30%;
    justify-content: center;
    margin-top:5%;
    margin-left:auto;
    margin-right:auto;
    margin-bottom: 1rem;
    padding:1rem;
    color:white;
    background-color:midnightblue;

}
.testItem-p{
    font-weight:500;
    width:50%;
    padding-top:2%;
    padding-bottom:2%;
    margin:auto;
    margin-bottom:50px;
    border-bottom:5px solid midnightblue;

}
.nav-header{
    width:100%;
    text-align: center;
    background-color: salmon;
}
.user-profiles{
    text-align: center;
    margin-top:10%;
}
.form-items{
    width:60%;
    margin-left:auto;
    margin-right: auto;
    overflow: hidden;
    margin-top:10%;

}

.form-container{
    width:100%;
    height:100%;
    position: relative;
    margin-top:1.5rem;
    margin-bottom:2rem;
  
}
.form-container input{
    width:100%;
    height:auto;
    position: relative;
    margin-top:.4rem;
    margin-bottom:.7rem;
    outline:none;
    border:none;
    pointer:cursor;
    font-size: 15px;
    font-weight:600;

}

.form-label{
    
    width:100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
    pointer-events: none;
    border-bottom:3px solid lightgray;
}

.form-container input:disabled {
    color:lightgrey!important;
}

.form-label::after{
    content:'';
    width:100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
    border-bottom:2px solid darkslateblue;
    transform: translateX(-150%);
  
}
.label-push{
    position: absolute;
    top: 0;
    left: 0;
    width:100%;
    pointer-events: none;
    color:lightgray;
    font-size:15px;
}
.submit-btn{
    cursor:pointer;
    background-color: white;
    border:2px solid black;
    font-weight: 700;
    color:black;
    border-radius: 25px;
    padding:.5rem 1rem .5rem 1rem;
}
.submit-btn:hover,.submit-btn:disabled{
    background-color: black;
    color:white;
}
.submit-btn:disabled{
    cursor:default;
}


.form-container input:focus + .form-label::after, .form-container input:not(:placeholder-shown) + .form-label::after{
    transform:translateX(0%);
    transition:0.2s ease all;
}

.form-container input:focus + .form-label .label-push, .form-container input:not(:placeholder-shown) + .form-label .label-push{
    transform:translateY(-100%);
    color:midnightblue;
    transition:.3s ease all;
}


@media only screen and (max-width: 600px) {
    h1{
        font-size: 20px;
    }
    .testItem-Header{
        padding:1rem;
        width:90%;
    }
    .contact-btn{
    margin-left:0;
    }

    .form-item{
        width:90%;
        height:100%;
    }
}